$primary: #dddddd !default; $secondary: #222222 !default; $tertiary: #0f82af !default; $quaternary: #c14924 !default; $header_background: #111111 !default; $header_primary: #dddddd !default; $highlight: #a87137 !default; $danger: #e45735 !default; $success: #1ca551 !default; $love: #fa6c8d !default;  $public_image_path: "https://d2723j7i5e2etm.cloudfront.net/ru/overwatch/images";  @import "common/foundation/variables"; @import "common/foundation/mixins"; .modal .modal-body.hosted-enable-ssl {
  p {
    font-size: 1em;
    &.start-subscription-notice {
      color: var(--danger);
    }
  }
}

.admin-plugins-howto,
.modal.start-backup-modal .modal-body .backup-no-uploads {
  display: none;
}

.admin-backups {
  .backup-message,
  .btn-rollback,
  .btn-restore {
    display: none;
  }
}

.hosted-site-debug-bar {
  border: 1px solid var(--danger);
  padding: 0.25em 0.25em 0 0.25em;
  margin-bottom: 1em;

  .actions {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .btn {
    margin-bottom: 0.25em;
    margin-right: 0.25em;
  }
}

.hosted-trial-welcome {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  border: 1px solid var(--primary-low);
  padding: 1.2em;
  margin: 0.25em 0 1em 0;

  .start-subscription-btn {
    margin-top: 0.5em;
    flex-grow: 0;
    flex: 1;
  }
}

.hosted-trial-expired {
  .hosted-trial-expired-description {
    margin: 0 0 0.5em 0;
  }
}

.hosted-sections {
  display: flex;
  flex-direction: row;
  margin: 1em 0 2em 0;

  p {
    margin-bottom: 0.5em;
  }

  .subtitle {
    font-size: var(--font-0);
    margin: 0;
  }

  .left-section {
    flex: 1;
    margin-right: 1em;

    .subscriptions {
      margin-bottom: 0.5em;
      padding-right: 1em;
    }

    .actions {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 1em;

      .btn {
        margin-right: 0.5em;
        margin-bottom: 0.5em;
      }
    }

    .billing-section {
      display: flex;
      flex-direction: row;

      .payment-method {
        flex: 1;
        margin-right: 1em;
      }

      .payment-method .btn + .btn {
        margin-top: 0.75em;
      }

      .billing-email {
        flex: 1;
        margin-left: 1em;
      }
    }
    .receipts-section {
      margin-top: 1em;
    }
  }

  .right-section {
    flex: 1;
    margin-left: 1em;

    .usage-section {
      .usage {
        margin-bottom: 1.25em;

        .header {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 0.25em;

          .status {
            color: var(--primary-high);
          }
        }
      }

      .usage.trial .hosted-trial-expiring {
        margin-top: 0.25em;
      }
    }
  }

  .support-instructions,
  .cancellation {
    list-style: none;
    margin: 0;
    padding: 0;

    .text {
      margin-left: 0.25em;
      margin-right: 0.25em;
    }
  }
  .support-instructions .text {
    font-weight: 700;
  }
  .cancellation .d-icon-heart {
    visibility: hidden;
  }
}

.hosted-invoice-details {
  .error,
  .invoice-details-error {
    color: var(--danger);
  }
  .invoice-details-error {
    margin-bottom: 0;
  }
}

.address-and-tax-sections {
  display: flex;
  flex-direction: row;

  .address-section {
    flex: 1;
    margin-right: 1em;
  }

  .tax-section {
    flex: 1;
  }

  .hs-tax-type-select {
    margin-bottom: 9px;
  }

  input,
  .select-kit {
    display: block;
    margin-top: 9px;
    margin-bottom: 0;
  }
}

@include breakpoint(tablet) {
  .hosted-sections,
  .hosted-sections .left-section .billing-section {
    flex-direction: column;

    .payment-method,
    .billing-email {
      margin-bottom: 1em;
    }

    .payment-method .btn + .btn {
      margin-top: 0;
    }
  }

  .hosted-sections .right-section {
    padding: 0;
  }

  .hosted-sections {
    .right-section,
    .left-section .billing-section .billing-email {
      margin-left: 0;
    }
  }

  .address-and-tax-sections {
    flex-direction: column;

    .tax-section {
      margin-top: 2em;
    }
  }
}

.static-progress-bar {
  .progress-bar-background {
    background: var(--primary-low);
    height: 32px;
    overflow: hidden;

    .progress-bar {
      height: 100%;
      background: var(--tertiary);
    }
  }

  &.is-over-limit {
    .remaining {
      color: var(--danger);
      font-weight: 700;
    }

    .progress-bar {
      background: var(--danger);
    }
  }
}

// change domain modal
.hosted-change-domain {
  .hosted-new-domain-label {
    align-items: center;
  }
  .hosted-new-domain-input {
    margin-bottom: 0;
    margin: 0 5px;
  }
  .dns-instructions {
    font-size: 0.85em;
    .centered {
      text-align: center;
      margin: 5px 0 0;
    }
    .mock-dns-form {
      display: inline-block;
      margin: 1em auto;
    }
    .mock-dns-form .mock-dropdown,
    .mock-dns-form .mock-text-input {
      float: left;
      display: inline-block;
      margin: 0 3px 3px 0;
      border: solid 1px #ccc;
      border-radius: 3px;
    }
    .mock-dns-form .mock-dropdown .text,
    .mock-dns-form .mock-text-input .text {
      display: inline-block;
      padding: 4px 8px;
    }
    .mock-dns-form .mock-dropdown .control {
      padding: 4px 8px;
      display: inline-block;
      border-left: solid 1px #ccc;
    }
    .mock-dns-form .mock-dropdown .control .fa {
      color: #ccc;
    }
  }
}

.hosted-change-domain-freesite {
  max-width: 450px;
  text-align: center;
  .btn {
    margin-top: 0.5em;
    margin-bottom: 1em;
  }
}

.hosted-support-options {
  max-width: 275px;
  .kb-image {
    transition: box-shadow 0.3s;
    margin: 5px 10px 16px;
    background: var(--primary-high);
    border: 1px solid var(--primary-low);
  }
  .kb-image:hover {
    box-shadow: 0 0 3px var(--primary-high);
  }
  .support-link {
    padding-left: 12px;
  }
}

// invoices modal
.modal.invoices-modal {
  .modal-inner-container {
    max-width: 800px;
    .modal-body {
      // !important needed because we use it in core in /common/base/modal.scss on
      // line #146
      max-height: 450px !important;
    }
  }
  tr:last-of-type {
    border-bottom: none;
  }
  .hosted-invoices-table {
    width: 100%;
    td,
    th {
      padding: 0.5rem 1rem;
    }
  }
  .btn-default {
    white-space: nowrap;
  }
}

.hosted-suggested-upgrade {
  p {
    margin-top: 0.5em;
  }
}

.hosted-line-items-table {
  td.amount {
    text-align: right;
    font-weight: 700;
    padding-left: 1em;
  }
  td.description {
    font-style: italic;
  }
}

.admin-invite-page {
  body::before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    position: fixed;
    background: linear-gradient(
        90deg,
        rgba(196, 180, 0, 0.5),
        rgba(8, 182, 249, 0.5)
      ),
      var(--secondary);
    background-size: 400% 400%;
    animation: gradientAnimation 10s ease infinite;
    @media (prefers-reduced-motion: reduce) {
      animation: none;
    }
  }
}

.admin-invite {
  background-color: var(--secondary);
  width: 50%;
  margin: 5em auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 0.5em;
  border: 1px solid var(--primary-low-mid);
  h2 {
    text-align: center;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    .word {
      margin-right: 0.3em;
    }
  }
  .col-image {
    margin: 0;
  }
}

.desktop-view {
  .admin-invite {
    h2 {
      font-size: 2.5em;
    }
    width: 40%;
    margin: 2em auto;
    padding: 2em;
    @media screen and (max-width: 1200px) {
      width: 50%;
    }
    @media screen and (max-width: 960px) {
      width: 60%;
    }
    @media screen and (max-width: 767px) {
      width: 70%;
    }
    .col-image {
      width: unset;
      .welcome-image {
        margin: 10px 0;
        width: 100px;
      }
    }
    .col-form {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-left: 0;
      p {
        width: 80%;
        text-align: center;
      }
      form {
        width: 75%;
        input {
          width: 100%;
        }
        .tip {
          display: inherit;
        }
      }
    }
  }
}

.mobile-view {
  .admin-invite {
    width: calc(90% - 2em);
    margin: 2em auto;
    padding: 2em 1em;
    position: relative;
    .col-image {
      display: block;
    }
    .col-form {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-left: 0;
      p {
        text-align: center;
      }
      form {
        width: 90%;
        input {
          width: 100%;
        }
        .tip {
          display: inherit;
        }
      }
    }
    .welcome-image {
      margin: 5px auto;
      width: 75px;
    }
  }
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

#global-notice-overdue-payment-notice {
  background-color: #f00;
  color: #fff;
  .btn {
    color: #f00;
    background-color: #fff;
  }
}
